import React from 'react';
import _ from 'lodash';
import Cookies from 'js-cookie';
import { inject, observer } from 'mobx-react';
import { Dimmer, Loader } from 'semantic-ui-react';
import { SiteData, withRouteData } from 'react-static';

import Page from '@stoplight/format-hubs/src/components/Page/viewer';

import PageStore from '../stores/page';

class HubPage extends React.Component {
  authRequired = () => {
    // Don't check auth when generating static HTML
    if (typeof window === 'undefined') return;

    const { isAuthRequired } = this.props;
    const isLoggedIn = Cookies.get('_session_token');

    return isAuthRequired && !isLoggedIn;
  };

  componentWillMount() {
    if (!this.authRequired()) return;

    window.location.pathname = '/_/login';
  }

  componentDidMount() {
    this.scrollToHash(_.get(this.props.routerStore, 'location.hash'));
  }

  componentWillUpdate(nextProps) {
    const { routerStore } = this.props;
    const nextPath = _.get(nextProps.routerStore, 'location.pathname');

    if (nextPath === _.get(routerStore, 'location.pathname') || !this.authRequired()) return;

    window.location.pathname = '/_/login';
  }

  componentDidUpdate(prevProps) {
    const currentLocation = _.get(this.props.routerStore, 'location') || {};
    const prevLocation = _.get(prevProps.routerStore, 'location') || {};

    const hasChangedHash = currentLocation.hash !== prevLocation.hash;

    if (hasChangedHash && currentLocation.hash) {
      this.scrollToHash(currentLocation.hash);
    }
  }

  scrollToHash(hash = '') {
    setTimeout(() => {
      const elementId = hash.replace('#', '');
      const element = document.getElementById(elementId);

      if (element) {
        element.scrollIntoView({ behavior: 'smooth', block: 'start', inline: 'nearest' });
      }
    }, 500);
  }

  render() {
    if (this.authRequired()) {
      return (
        <div className="flex-1 relative">
          <Dimmer active>
            <Loader content="Logging out..." />
          </Dimmer>
        </div>
      );
    }

    const { page = {}, sidebarTree = {}, crumbs, routerStore, whitelabel, config } = this.props;

    const store = new PageStore({
      id: page.path,
      page,
      crumbs,
      config,
      sidebarTree,
      routerStore,
      whitelabel,
    });

    return <Page id={store.id} store={store} />;
  }
}

const InjectedHubPage = inject((stores, props) => {
  const { routerStore } = stores;

  return {
    routerStore,
  };
})(observer(HubPage));

export default withRouteData(routeData => {
  return <SiteData {...routeData} component={InjectedHubPage} />;
});



