import React from 'react';
import _ from 'lodash';
import ReactNoSSR from 'react-no-ssr';
import { hot } from 'react-hot-loader';
import { Router } from 'react-static';
import { Provider as MobxProvider } from 'mobx-react';

import createBrowserHistory from 'history/createBrowserHistory';
import createMemoryHistory from 'history/createMemoryHistory';

import { syncHistoryWithStore } from '@stoplight/router';

import Alert from '@stoplight/components/src/Alert';
import ErrorBoundary from '@stoplight/components/src/ErrorBoundary';

import { alert } from '@stoplight/utils/src/alert';
import { parseQuery } from '@stoplight/utils/src/query';

if (process.env.NODE_ENV !== 'test') {
  require('@stoplight/styles/src/index.styl');
  require('@stoplight/ui/lib/index.css');
}

import Hub from './containers/Hub';
import { createRootStore } from './stores';

const canUseDOM = !!(
  typeof window !== 'undefined' &&
  window.document &&
  window.document.createElement
);

if (typeof window !== 'undefined') {
  // expose certain libraries to custom user JS scripts
  window.SL = {
    alert,
  };
}

const App = props => {
  let rootStoreOptions = {};

  let targetHistory;
  if (canUseDOM) {
    rootStoreOptions = {
      location: window.location,
      globalQueryParams: _.pick(parseQuery(window.location.search), ['__build_id']),
    };

    targetHistory = createBrowserHistory();
  } else {
    targetHistory = createMemoryHistory();
  }

  const rootStore = createRootStore(rootStoreOptions);
  const history = syncHistoryWithStore(targetHistory, rootStore.stores.routerStore);

  if (canUseDOM) {
    _.set(window, '__SL.rootStore', rootStore);
    _.set(window, '__SL.history', targetHistory);
  }

  return (
    <MobxProvider {...rootStore.stores}>
      <Router history={history} autoScrollToTop={false} autoScrollToHash={false}>
        <ErrorBoundary message="Click the reload button below to continue.">
          <Hub />
          <Alert />
        </ErrorBoundary>
      </Router>
    </MobxProvider>
  );
};

export default hot(module)(App);



