import _ from 'lodash';

export default class HubViewer {
  page = {
    // path,
    // parsedPath,
    // title,
    // route,
    // data,
    // isSubpage,
    // rootPagePath,
    // meta,
  };

  crumbs = [];
  sidebarTree = [];

  constructor(props) {
    const { id, page, crumbs, config, sidebarTree, routerStore, whitelabel = false } = props;

    this.id = id;
    this.page = page;
    this.crumbs = crumbs || [];
    this.config = config || {};
    this.sidebarTree = sidebarTree || [];
    this.routerStore = routerStore;
    this.whitelabel = whitelabel;
  }

  get hasSidebar() {
    return !!this.sidebarTree.length;
  }

  get activePage() {
    return {
      parsedPath: this.currentParsedPath,
      treePath: this.currentParsedPath,
      data: this.page,
    };
  }

  get activeSubpage() {
    return this.activePage;
  }

  get currentPath() {
    return _.compact(_.split(this.page.path, '/'));
  }

  get basePath() {
    // TODO: Do we need this?
    // return this.page.rootPagePath || this.page.path;
    // console.warn('hub-builder/stores/page.js basePath has not been implemented.');
    return '';
  }

  get currentRelativePath() {
    return _.trimStart(this.page.path, this.page.rootPagePath);
  }

  get currentParsedPath() {
    return this.page.parsedPath;
  }

  buildViewPath = (path = '') => {
    return path;
  };

  checkSidebarItemIsActive = item => {
    if (typeof document === 'undefined') return;

    return this.page.path === item.path;
  };

  checkSidebarChildIsActive = folder => {
    if (typeof document === 'undefined' || !folder) return;

    // Check to see if our current path starts with the folder's path
    let reg = new RegExp(`^${folder.path}`);

    return reg.test(this.page.path);
  };

  redirect = page => {
    // TODO: Do we need this function?
    console.warn('hub-builder/stores/page.js redirect has not been implemented.');
  };

  getResults = searchQuery => {
    // TODO: Should return search results
    // return searchResults({
    //   data: this.parsed,
    //   expression: searchQuery,
    // });
    console.warn('hub-builder/stores/page.js getResults has not been implemented.');
    return [];
  };

  goToNestedPath(path) {
    const location = _.get(this.routerStore, 'location') || {};

    if (path !== location.pathname) {
      this.routerStore.replace(this.buildViewPath(path));
    }
  }
}



