import React from 'react';
import { Button } from 'semantic-ui-react';

export default () => {
  return (
    <div className="NotFound px-4 h-full flex flex-col items-center justify-center m-auto w-full">
      <h2 className="c-muted">Page Not Found</h2>

      <a className="mt-4" href="/">
        <Button fluid secondary content="Take Me Home" />
      </a>
    </div>
  );
};



