import React from 'react';
import Routes from 'react-static-routes';
import _ from 'lodash';
import { SiteData, withRouteData } from 'react-static';

import HubHeader from '@stoplight/format-hubs/src/components/HubHeader/viewer';

import ErrorBoundary from '@stoplight/components/src/ErrorBoundary';

import * as Bugsnag from '@stoplight/utils/src/bugsnag';
import GoogleAnalytics from '@stoplight/utils/src/googleAnalytics';
import Intercom from '@stoplight/utils/src/intercom';
import { initLogging } from '@stoplight/utils/src/logging';
import { buildHubDropdownOptions } from '@stoplight/format-hubs/src/utils';

class Hub extends React.Component {
  componentDidMount() {
    const { gaKey, intercomKey } = this.props;

    Bugsnag.init();

    initLogging();

    // Init Stoplight GA
    GoogleAnalytics.init();

    if (gaKey) {
      // Init Hub GA
      GoogleAnalytics.init(gaKey);
    }

    if (intercomKey) {
      Intercom.init(intercomKey, { hideLauncher: false });
    }
  }

  render() {
    const { id, hubHeader = {}, page = {}, sidebarTree, theme, hasHeader } = this.props;

    const hubElems = [];

    hubElems.push(
      <HubHeader
        key="1"
        id={id}
        theme={theme}
        hasHeader={hasHeader}
        nav={hubHeader.nav}
        activePagePath={page.rootPagePath || page.path}
        buildPath={p => p}
        mobileProps={{
          hasSidebar: !_.isEmpty(sidebarTree),
          options: buildHubDropdownOptions(hubHeader),
          // TODO: getResults is not currently implemented
        }}
      />
    );

    hubElems.push(
      <ErrorBoundary
        key="2"
        className="flex flex-1 relative"
        message="Click the reload button below to continue."
      >
        <Routes />
      </ErrorBoundary>
    );

    hubElems.push(
      <div key="3" className="pin absolute flex z--1" role="presentation">
        <div className="h-full flex-1 bg-grey-lightest" />
        <div className="h-full flex-1 bg-white" />
      </div>
    );

    return <div className="Hub flex flex-col h-full">{hubElems}</div>;
  }
}

export default withRouteData(routeData => {
  return <SiteData {...routeData} component={Hub} />;
});



